#profbookIcon {
    margin: auto;
    margin-top: 37px;
    margin-bottom: 37px;
    width: 150px;
}

.title {
    text-align: center;
    font-size: 50px;
}

.button {
    display: block;
    margin: auto;
    width: 200px;
}
#test {
    max-width: 600px;
}

ion-col {
    padding: 0 2px 0 2px;
}

ion-grid {
    padding: 0 0 5px 0;
}

#peopleList {
    max-width: 800px;
    margin: auto;
}

.profilItem {
    cursor: pointer;
}

.profilItem:hover {
    --ion-background-color: #EEE;
}

#profilContainer {
    max-width: 800px;
    margin: auto;
    margin-top: 1em;
    padding: 0 .5em 0 .5em;
}

.avatar {
    width: 100px;
    height: 100px;
    margin: auto;
    margin-bottom: 1em;
}

.info {
    margin: 0 1em 0 1em;
    text-align: justify;
}

.popover-viewport>h2 {
    margin-left: 1em;
}

.menuButton {
    cursor: pointer;
}

.menuButton:hover {
    --ion-background-color: #EEE;
}

.iconButton {
    cursor: pointer;
    border-radius: 40%;
    -webkit-transition: all .3s;
    transition: all .3s;
}
.iconButton2 {
    cursor: pointer;
}

.iconButton:hover {
    background-color: rgba(0, 0, 0, 0.2);
}

ion-fab-button {
    position: relative;
    right: 20px;
    bottom: 10px;
}

#imgUploadButton {
    font-size: 20px;
    cursor: pointer;
    height: 35px;
    width: 35px;
    opacity: 0;
}

#imgUploadContainer {
    position: absolute;
    top: 16px;
    left: 51%;
    cursor: pointer;
}

#imgUploadContainer>span {
    background-color: black;
    color: white;
    display: inline-block;
    padding: 5px 10px 10px 12px;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    opacity: .75;
    border-radius: 50%;
    cursor: pointer;
}

